.GroupPage {
    display: flex;
    flex-direction: column;
    align-items: center;
}



.GroupPage h1 {
    margin-top: 10px;
}

.GroupPage .about {
    margin-bottom: 20px;
}

.GroupPage .main {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.GroupPage .subjects {
    width: 200px;
}

.GroupPage .subjects button {
    display: block;
    margin: 5px 0;
    padding: 8px;
    width: 100%;
    cursor: pointer;
    background-color: #4caf50;
    color: #fff;
    border: none;
    border-radius: 4px;
}

.GroupPage .subjects button:hover {
    background-color: #45a049;
}